import axios from 'axios';
const url = "http://localhost:5000/api/posts";

export const allPosts = async () => {
    try {
        const response = await axios.get(url);
        const data = response.data;
        return data.map(post => ({
            ...post,
            createdAt: new Date(post.createdAt)
        }))

    } catch (err) {
        console.log(err.message);
    }
}

export const addPost = async ({title, body}) => {
    try {

        return await axios.post("http://localhost:5000/api/posts/create", { title, body});

    } catch (error) {
        console.log(error);
    }
}

export const updatePost = async (id) => {
    try {
        return await axios.patch(`http://localhost:5000/api/posts/${id}`, {title: "TEST", body: "TESTING TESTING TESING"})
    } catch (error) {
        console.log(error);
    }
}

export const deletePost = async (id) => {
    try {
        return await axios.delete(`http://localhost:5000/api/posts/${id}`)
    } catch (error) {
        console.log(error);
    }
}