<template>
  <div class="container">
    <h1 class="display-1">{{ msg }}</h1>
    <div class="posts-section">
      <div class="add-post">
       <form>
         <div class="form-group">
          <label for="title" class="form-label">Title</label>
        <input type="text" v-model="title" id="title" class="form-control">
       </div>
       <div class="form-group">
          <label for="msg" class="form-label">Body</label>
        <textarea v-model="body" id="msg" class="form-control"></textarea>
       </div>
        <button @click="newPost" class="btn btn-primary">Add</button>
       </form>
      </div>

      <div class="post" v-for="post in posts" :key="post._id">
        <!-- <h3>{{ `${post.createdAt.getMonth()} / ${post.createdAt.getFullYear()}` }}</h3> -->
        <h2 class="title"> {{ post.title }}</h2>
        <p class="body"> {{ post.body}} </p>
        <button @click="delPost(post._id)" class="btn btn-danger">Delete</button>
      </div>
    </div>

  </div>
</template>

<script>
import { allPosts, addPost, updatePost, deletePost } from "../PostService";
export default {
  name: 'PostComponent',
  props: {
    msg: String
  },
  data() {
    return {
      posts: [],
      error: '',
      title: '',
      body: ''
    }
  },
  async mounted() {
    try {
      this.posts = await allPosts();
    } catch (err) {
      this.error = err;
    }
  },

  methods: {
    // Create New Post
    async newPost() {

      try {
        const response = await addPost({
        title: this.title,
        body: this.body
      });
        this.posts.push(response.data);
        this.title = "";
        this.body = "";
      } catch (error) {
        console.log(error);
      }
    },

    // update post
    async updatePost(id) {
      try {
        const response = await updatePost(id);
        console.log("UPDATE:", response.data);
      } catch (error) {
        console.log(error);
      }
    },

    // delete post
    async delPost(id) {
      try {
        const item = await deletePost(id);
        this.posts.splice(item, 1)

      } catch (error) {
        console.log(error);
      }
    }
  }
}
</script>

